<template>
      <div id="um-idcard" class="anim" :style="{backgroundColor: background}">
        <div class="header">
            <span id="lang_header">LOS SANTOS</span>
            <span id="cardtype">{{cardName}}</span>
        </div>
        <div class="content">
            <div class="icon">
                <img id="mugshot" src="@/assets/player.png">
            </div>
            <div class="text">
                <ul>
                    <li>
                        <span id="lang_lastname">Lastname</span>
                        <span id="lastname" class="info">Doe</span>
                    </li>
                    <li>
                        <span id="lang_firstname">Firstname</span>
                        <span id="name" class="info">John</span>
                    </li>
                    <li>
                        <span id="lang_dob">Date of Birth</span>
                        <span id="dob" class="info">{{ day }}.{{ month }}.{{ year }}</span>
                    </li>
                    <li>
                        <span id="lang_sex">Sex</span>
                        <span id="sex" class="info">F</span>
                    </li>
                    <li>
                        <span id="lang_nat">Nationality</span>
                        <span id="nationality" class="info">Turkish</span>
                    </li>
                </ul>
            </div>
        </div>
        <div class="footer">
         <div class="signature">
            <span id="sign">DoeJohn</span>
         </div>
         <div class="flagsandimg">
            <img id="smallmugshot" src="@/assets/player.png">
            <img :src="'https://flagsapi.com/' + selectedOption + '/flat/32.png'">
         </div>
        </div>
    </div>
</template>

<script>
export default {
    name : 'IDCard',
    props: {
        background: String,
        cardName: String,
        firstName: String,
        selectedOption: String,
    },
    data() {
    return {
      day: '',
      month: '',
      year: ''
    }
  },
  mounted() {
    const today = new Date();
    this.day = today.getDate();
    this.month = today.getMonth() + 1;
    this.year = today.getFullYear();
  },
}
</script>

