<template>
  <div class="codefinish">
    <small>config</small>
    <pre>
      <small>--[[ Add: um-idcard > config.lua > Config.Licenses = {} ]]</small>
      <code>
        ["{{ trimmedCardName }}"]: {
          header: "{{ cardName }}",
          background: "{{hexColor}}"
        },
      </code>
    </pre>
    <small>flags</small>
    <pre>
      <small>-- Download and place it inside the web > flags folder without changing the name.</small>
      <code>
        https://flagsapi.com/{{selectedOption}}/flat/32.png
      </code>
    </pre>
    <small>ox-inventory</small>
    <pre>
      <small>--[[ Add: ox-inventory > data > items.lua ]]</small>
      <code>
        ['{{ trimmedCardName }}'] = {
          label = '{{ cardName }}',
          weight = 0,
          stack = false,
          close = true,
          description = "Type of this ID card: {{ cardName }}",
          client = {image = '{{ trimmedCardName }}.png'}
	},
      </code>
    </pre>
    <small>qb-inventory - lj-inventory</small>
    <pre>
      <small>--[[ Add: qb-core > shared > items.lua ]]</small>
      <code>
        ['{{ trimmedCardName }}'] = {
          ['name'] = '{{ trimmedCardName }}',
          ['label'] = '{{ cardName }}',
          ['weight'] = 100,
          ['type'] = 'item',
          ['image'] = '{{ trimmedCardName }}.png',
          ['unique'] = true,
          ['useable'] = true,
          ['shouldClose'] = true,
          ['combinable'] = nil,
          ['description'] = "Type of this ID card: {{ cardName }}"
        },
      </code>
    </pre>
  </div>
</template>

<script>
export default {
  name: 'CodeFinish',
  props: {
    cardName: String,
    hexColor: String,
    selectedOption: String,
  },
  computed: {
    trimmedCardName() {
      return this.cardName.trim().replace(/\s+/g, '').toLowerCase()
    }
  }
}
</script>

<style scoped>
.codefinish {
  display: flex;
  align-items: center;
  flex-direction: column;
}
pre {
  background-color: #202020; 
  padding: 10px; 
  color: #b9b77c;
  font-size: 14px; 
  font-family: monospace;
  border-radius: 7px;
  text-align: left;
  width: 50%;
}
small {
  background: #1f1f1f6e;
    padding: 1vh;
}
</style>
